<template>
 <v-timeline :dense=mobile>
    <v-timeline-item
      v-for="job in filteredJobs"
      :key="job.position.position"
    >
      <template v-slot:opposite v-if=!mobile>
        <v-chip color="primary" outlined>
          {{ job.position.start }} - {{ job.position.end || t('to date') }}
        </v-chip>
      </template>
      <v-card elevation="6">
        <v-card-title class="primary pa-0">
          <v-container fluid>
          <v-row no-gutters>
            <v-col>
            <v-treeview
              dense
              open-on-click
              hoverable
              item-children="formerly"
              item-key="name"
              :items="[job.company]"
              class="white--text"
            >
              <template v-slot:prepend="{ item }">
                <span v-if="!companies.includes(item)" class="text-capitalize">
                  {{ t('formerly') }}:
                </span>
              </template>

              <template v-slot:label="{ item }">
                <a
                  v-if="item.url"
                  :href="item.url ? item.url : undefined"
                  class="white--text text-decoration-none"
                  target="_blank"
                >
                  {{ item.name}}
                  <span v-if="companies.includes(item)" class="text-capitalize">
                    [{{ item.location }}]
                  </span>
                </a>
                <span v-else>
                  {{ item.name }}
                </span>
              </template>
            </v-treeview>
            </v-col>
            <v-col class="d-flex flex-grow-1 align-start justify-end">
              <v-tooltip left v-if="job.position.remote">
                <template v-slot:activator="{ on }">
                  <v-icon v-on=on v-text="'$remote'" />
                </template>
                {{ t('remote') }}
              </v-tooltip>
            </v-col>
          </v-row>
          <v-row no-gutters>
            <span class="font-weight-light text-caption shadowed--text pl-8">
              {{ job.company.shortDescription }}
              <span v-if="job.company.headCount">
                ({{ job.company.headCount }} {{ t('employees') }})
              </span>
            </span>
          </v-row>
          </v-container>
        </v-card-title>

        <v-card-text>
          <div v-if=mobile>
            {{ job.position.start }} - {{ job.position.end || t('to date') }}
          </div>
          <p class="text-subtitle-2 pt-2 text--primary">
            {{ job.position.position }}
          </p>
          <li
            v-for="(responsibility, index) in job.position.responsibilities"
            :key="index"
          >
            {{ responsibility }}
          </li>
          <p class="text-capitalize my-2">
            {{ t('technologies') }}:
          </p>
          <v-chip-group column>
            <v-chip
              v-for="tag in job.position.technologies"
              :key="tag"
              small
              @click="filterTag(tag)"
            >
              {{ tag }}
            </v-chip>
          </v-chip-group>
        </v-card-text>
      </v-card>

    </v-timeline-item>

    <v-footer fixed color="secondary" v-if="tag">
      <v-row class="subtitle-2 white--text" justify="center" no-gutters>
        {{ t('Filtering by') }}:
        <v-chip
          color="primary"
          close
          small
          class="ml-2"
          @click:close="tag = null"
        >{{ tag }}</v-chip>
      </v-row>
    </v-footer>
 </v-timeline>
</template>

<script>
export default {
  name: 'cvJobs',
  props: {
    data: {
      type: Array[Object],
      default: () => [],
    },
  },
  data: () => ({
    tag: null,
  }),
  computed: {
    companies() {
      return this.data.map((item) => item.company);
    },
    jobs() {
      return this.data.flatMap((job) => (
        job.positions.map((position) => (
          {
            company: job.company,
            position,
          }
        ))
      )).reverse();
    },
    filteredJobs() {
      return this.tag
        ? this.jobs.filter((job) => job.position.technologies.includes(this.tag))
        : this.jobs;
    },
  },
  methods: {
    filterTag(tag) {
      this.tag = tag;
    },
  },
};
</script>

<style lang="scss" scoped>
  .v-chip--active {
    color: var(--v-primary-base);
  }
</style>
